body {
  height: 100vh;
  background-color: #f2f2f2;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  position: fixed;
  top: 20px;
  left: 20px;
}

.form {
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
}

label {
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

input[type="number"] {
  width: 100%;
  border: none;
  border-radius: 5px;
  margin-top: 5px;
  margin-bottom: 15px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.btn:hover {
  background-color: #3e8e41;
}

